.pfp-name-input {
    width:280px;
    height: 34px;
    background-color: rgb(73, 73, 73);
    border-radius: 7px;
    color: rgb(187, 187, 187);
    padding:7px 12px;
    font-size: 14px;
    font-weight: 500;
}

.edit-pfp-img-ctn {
     position:relative;

}

