    cursor: pointer;
    user-select: none;

} */

.pfp-img-layer {
    z-index: 2;
    width: fit-content;
    height: fit-content;
    position: absolute;
    top: 0 ; 
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    font-size: 72px ;
    font-weight: 500;
    color: rgb(255, 168, 6);
    cursor: pointer;
    user-select: none;
}

.edit-pic-btn {
    width:100%;
    text-align: center;
    height: fit-content;
    padding: 6px 30px;
    border-radius: 8px;
    cursor: pointer;
    font-size: 15px;
    font-weight: 600;
    transition: all;
    transition-duration: .3s;
}

.edit-pic-btn:hover {
    background-color: rgb(234, 152, 0);
}