.maiN {
    z-index: 6;
}

.nav-ctn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* padding: 20px 50px; */
    width: 100%;
    height: 80px;
    position: sticky;
    margin:auto;
    top:0;
    z-index: 4;
}

.menu-title {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 30px;
}

.icn-mn {
    width: fit-content;
    height: fit-content;
    font-size: 22px;
    transition: all;
    transition-duration: .4s;
    cursor: pointer;
    user-select: none;
    width: fit-content;
    height: fit-content;
}

.icn-mn-off {
    width: fit-content;
    height: fit-content;
display: none;
}

.icn-mn:hover {
    color: #870087;
}

.pfp-nav{
    margin-right: 60px;
    width:50px;
    height: 50px;
    cursor: pointer;
    user-select: none;
    transition: all;
    transition-duration: .2s;
    display: flex;
    justify-content: center;
    align-items: center;
}

.pfp-nav:hover {
    filter: brightness(.9);
}

.pfp-n{
    width:50px;
    height:50px;
    object-fit: cover;
    object-position: center;
    border-radius: 50%;
    opacity: .9;
    filter: brightness(1);
    transition: all;
    transition-duration: .3s;
}

.lgout {
    font-size: 14px;
    font-weight: 600;
}

/*Slide*/
.Slide-ctn {
    width: 18%;
    height: 100vh;
    position: fixed;
    left: 0;
    top:0;
    background-color: white;
    transition:all;
    transition-duration: .3s;
    z-index: 3;
}

.slide-ctn-off {
    width: 18%;
    height: 100vh;
    position: fixed;
    left: 0;
    top:0;
    background-color: white;
    transform: translateX(-500px);
    transition:all;
    transition-duration: .3s;
}

.slide-bg {
    /* background-image: linear-gradient(to bottom ,#fffdc9,#fce4bf,#fffdf9 ); */
background-color: rgb(255, 248, 230);
}

.slide-nav-ctn {
    margin-top: 100px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: 15px;
    font-weight: 500;
}

.line {
    width: 70%;
    margin: 30px auto;
    height: 1px;
    background-color: rgba(0, 0, 0, 0.523);
    opacity: .6;
}

.itm {
    user-select: none;
    cursor: pointer;
}

.i1:hover , .i2:hover , .i3:hover , .i4:hover {
    color: #ffa908;
}

.i1 , .i2 , .i3 , .i4 {
transition: all;
transition-duration: .3s;
}

