.srh-ctn {
    width: 50%;
    margin: 40px auto;
}

.srh-btn {
    padding: 11px;
    border-radius: 8px;
    background-color: #FFA500;
    font-size: 16px;
    margin-left: 10px;
    transition: all;
    transition-duration: .4s;
}

.srh-btn:hover {
    background-color:  #fcf512;
}

.my-carousel {
    width:85%;
    position: relative;
    margin: 0 auto;
    margin-top: 150px;
    object-fit: cover;
    object-position: center;
}

.my-carou-inner {
    height: 480px;
    width:100%;

}

.carou-img-ctn {
    height: 100%;
    width: 100%;
    position: relative;
}

.carou-pic{
    filter: brightness(.6);
    transition:all;
    transition-duration: .4s;
}
.carou-pic:hover{
    filter: brightness(.5);
}

.my-tooltip {
    width:fit-content;
    height:fit-content;
    padding: 5px 17px;
    border-radius: 16px;
    color:white;
    position: absolute;
    top:120px;
    left:0;
    right:0;
    margin: auto;
    z-index:3;
    font-size: 13px;
    font-weight: 600;
}

.carou-main-title {
    width:fit-content;
    height:fit-content;
    color:white;
    position: absolute;
    top:50px;
    left:0;
    right:0;
    margin: auto;
    z-index:3;
    font-size: 18px;
    font-weight: 600;
}

.carou-title {
    width:fit-content;
    height:fit-content;
    color:white;
    position: absolute;
    top:200px;
    left:0;
    right:0;
    margin: auto;
    z-index:3;
    font-size: 38px;
    font-weight: 600;
}

.carou-btn {
    width: fit-content;
    height: fit-content;
    padding: 8px 38px;
    border-radius: 5px;
    position: absolute;
    bottom:100px;
    left:0;
    right:0;
    margin: auto;
    z-index:2;
    font-size: 14px;
    font-weight: 400;
    transition: all;
    transition-duration: .3s;
}

.carou-btn-ui {
    border : solid 3px #FFA500;
}

.carou-btn-ui:hover {
    background-color: inherit;
    color:#ffc04a
}

.carou-btn-ui2 {
    border : solid 3px #fcf512;
    background-color: #fcf512;
}

.carou-btn-ui2:hover {
    background-color: inherit;
    color: #f8f240;
}

.carou-btn-ui3 {
    border : solid 3px #FF00FF;
    background-color: #FF00FF;
}

.carou-btn-ui3:hover {
    background-color: inherit;
    color: #ff4eff;
}

/*TOP POPULAR */
.popular-ctn {
    margin-top: 55px;

}

.p-txt {
    font-size: 22px;
    font-weight: 600;
    margin-left: 12px;
}

.books-ctn {
    height:fit-content;
    width: 97%;
    margin: 55px auto;
    display:flex;
}

.nm-book-ctn{
    width: fit-content;
    height: calc(270px + 110px);
    position: relative;

}

.nm-book {
    min-width: 170px;
    height: 270px;
    cursor:pointer;
    user-select: none;
}


::-webkit-scrollbar{
    display: none;
}

.my-tooltip-nm {
    width:fit-content;
    height:fit-content;
    padding: 3px 14px;
    border-radius: 16px;
    color:white;
    position: absolute;
    bottom:140px;
    left:0;
    right:0;
    margin:auto;
    z-index:3;
    font-size: 12px;
    font-weight: 600;
}

.nm-book-title{
font-size: 14px;
font-weight: 600;    
padding-left: 10px;
padding-top: 20px;
}

.nm-book-artist {
    font-size: 12px;
    font-weight: 400;
    color: rgba(231, 231, 231, 0.587);
    padding: 10px;
}

.rate {
    padding-left:  10px;
    padding-top: 8px;
    display: flex;
    align-items: center;
}

