.main-ctn {
    display: flex;
    justify-content: space-between;
    align-items: center;

}

.roles-ctn {
    width:50%;
    height:200px;
}

.role-bars {
    width: 70%;
    border:inherit;
    font-size: 15px;
    font-weight: 500;
    margin-left: 20px;
}


.Books-ctn {
    display: grid;
     grid-template-columns: repeat(3, minmax(0, 1fr)); 
     height:500px;
     overflow: auto;
    }

    @media (max-width: 1000px) {
        .Books-ctn {
            display: grid;
             grid-template-columns: repeat(2, minmax(0, 1fr)); 
             height:500px;
             overflow: auto;
            }
        
    }

    @media (max-width:940px) {
        .main-ctn {
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-direction: column;
        
        }
        .roles-ctn {
            display: flex;
            justify-content: center;
            align-items:center;
            width:50%;
            height:90px;
        }
        .Books-ctn {
            display: grid;
             grid-template-columns: repeat(3, minmax(0, 1fr)); 
             
             height:700px;
             overflow: auto;
            }
    }

    @media (max-width: 645px) {
        .main-ctn {
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-direction: column;
        
        }
        .roles-ctn {
            display: flex;
            justify-content: center;
            align-items:center;
            width:50%;
            height:90px;
        }
        .Books-ctn {
            display: grid;
             grid-template-columns: repeat(2, minmax(0, 1fr)); 
             
             height:700px;
             overflow: auto;
            }
    }
    @media (max-width: 427px) {
        .Books-ctn {
            display: grid;
             grid-template-columns: repeat(1, minmax(0, 1fr)); 
             height:700px;
             overflow: auto;
            }
    }