.err-btn {
    width: fit-content;
    height: fit-content;
    padding: 8px 30px;
    border-radius: 4px;
    font-size: 14px;
    font-weight: 500;
}

.er {
    font-size: 100px;
    width:fit-content;
    height : fit-content;
    text-align : center;
    font-weight: 600;
}

.er-content {
    font-size: 20px;
    font-weight : 600;
    width:fit-content;
}

@media (max-width:600px) {
    .er {
        font-size: 80px;
    }
    .er-content {
        font-size: 15px;
        font-weight : 600;
        width:fit-content;
    }
}

@media (max-width: 345px) {
    .er {
        font-size: 70px;
    }
    .er-content {
        font-size: 13px;
        font-weight : 600;
        width:fit-content;
    }
    .err-btn {
        width: fit-content;
        height: fit-content;
        padding: 8px 26px;
        border-radius: 4px;
        font-size: 14px;
        font-weight: 500;
    }
}