.profile-ctn {
    width: 95%;
    margin:90px auto;
    height: 200px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.profile {
    display: flex;
    align-items: center;
    gap:30px;
}

.pfp-img-ctn {
    width:150px;
    height: 150px;
}

.pfp-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    border-radius: 50%;

}

.pfp-name {
    font-size: 18px;
    font-weight: 500;
    width:300px;
}

.edit-btn {
    gap:12px;
    width:120px;
    height: 100px;
}

.edit-pfp-btn {
    width:100%;
    height: fit-content;
    padding: 5px 0;
    border: solid 2px  #FF00FF;
    color: #ef00ef;
    transition: all;
    transition-duration: .3s;
    border-radius: 6px;
    font-size: 15px;
    font-weight: 500;
    margin-bottom: 12px;
}

.edit-pfp-btn:hover {
    background-color: #FF00FF;
    color:white;
}

.log-btn {
    width:100%;
    height: fit-content;
    padding: 5px 0;
    border: solid 2px #FFA500;
    color: white;
    transition: all;
    transition-duration: .3s;
    border-radius: 6px;
    font-size: 15px;
    font-weight: 500;
}

.log-btn:hover {
    background-color: inherit;
    color: #FFA500;

}

/*FAV*/
.fav-ctn {
    width: 100%;
    margin:auto;
    height: fit-content;
    margin-top: 200px;
}

.library-ctn {
    width: 90%;
    height: fit-content;
    margin: auto;
    margin-top: 23px;
    display: grid;
	grid-template-columns: repeat(4, minmax(0, 1fr));
    padding: 10px;
    overflow: auto;
}

@media (max-width: 900px) {
    .library-ctn {
        width: 90%;
        height: fit-content;
        margin: auto;
        margin-top: 23px;
        display: grid;
        grid-template-columns: repeat(3, minmax(0, 1fr));
        padding: 10px;
        overflow: auto;
    }
}

