.book-poster {
    width: 100%;
    height: 100vh;
    position: relative;
    background-color: #fff;
}

.book-backdrop {
    
    filter:brightness(.4);
    width: 100%;
    object-fit: cover;
    object-position: center;
    height: 100%;
}

.img-lyr {
    z-index: 2;
    position: absolute;
    margin:auto;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.842)  , rgba(0, 0, 0, 0));
}

.Book-img {
    z-index: 3;
    position: absolute;
    width: 170px;
    height: 280px;
    top:0;
    bottom: 0;
    left: 100px;
    margin: auto;
    object-fit: cover;
    object-position: center;
    border-radius: 8px;
}

.backdrop-layer {
    display: flex;
    flex-direction: column;
    gap:40px;
    justify-content: center;
    position: absolute;
    width:fit-content;
    height:fit-content;
    z-index: 3;
    top:0;
    bottom: 0;
    left: 450px;
    margin: auto;

}

.backdrop-role {
    background-color:rgb(196, 13, 13);
    padding: 1px 16px;
    border-radius: 12px;
    width: fit-content;
    height: fit-content;
}

.paragraph-ctn {
    width:90%;
    margin: 120px auto;
    height: fit-content;
    text-align: justify;
    font-size: 18px;
    color:rgb(200, 200, 200);
}

.font1{
    font-size: 20px;
    font-weight: 500;
}

.font2{
    font-size: 16px;
    font-weight: 600;
}

.font3{
    font-size: 13px;
    font-weight: 600;
}

.font4{
    font-size: 16px;
    font-weight: 600;
    color: #ffffffc7;
}
.bm {
    position: absolute;
    z-index:3;
    top:-10px;
    right:60px;
    color:  #FFA500;
    font-size: 62px;
    transition: all;
    transition-duration: .3s;
    cursor: pointer;
    user-select: none;
}

.bm:hover {
    color:  #ffb52b;
}

.save-bm {
    display: block;
}

.saved-bm {
    display: none;
}

