.one-ctn {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
 
}

.title {
    font-size: 45px;
    font-weight: 500;
    width: fit-content;
    height: fit-content;
    margin: 10px 0;
}

.desc {
    font-size: 17px;
    font-weight: 500;
    width: fit-content;
    height: fit-content;
}

.form-ctn {
    margin-top: 40px;
    display: flex;
    justify-content: space-evenly;
    padding: 30px 10px;
}

.pfp-slt {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.pfp {
    width: 150px;
    height: 150px;
    background-color:rgb(51, 51, 51);
    border-radius: 50%;
    border : solid 1px #fe08fe;
    object-fit: cover;
    object-position: center;
}

.quest-pfp {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    margin: auto;
    color: rgb(39, 39, 39);
    font-size: 90px;
    font-weight: 600;
    width: fit-content;
    height:fit-content;
    user-select: none;
}

.pfp-btn {
    width:fit-content;
    height: fit-content;
    padding: 10px 30px;
    border-radius: 10px;
    font-size: 14px;
    font-weight: 500;
    color: rgb(39, 39, 39);
    background-color: #FF00FF;
    border:solid 2px  #FF00FF;
    transition: all;
    transition-duration: .3s;
    user-select: none;
    cursor: pointer;

}

.pfp-btn:hover {
    background-color: inherit;
    color:#FF00FF
}

.formdata {
    width: 50%;
}

.form-btn {
    width:fit-content;
    height: fit-content;
    padding: 10px 36px;
    border-radius: 8px;
    font-size: 15px;
    font-weight: 500;
    color: rgb(39, 39, 39);
    background-color: #FFA500;
    border:solid 2px  #FFA500;;
    transition: all;
    transition-duration: .3s;
    user-select: none;
    cursor: pointer;
}

.form-btn:hover {
    background-color: inherit;
    color:#FFA500
}

.txt-ctn {
    display: flex;
    margin-top: 10px;
}

.txt1 {
    font-size: 14px;
    font-weight: 400;
    color : rgb(242, 237, 237);

}

.txt2 {
    font-size: 14px;
    font-weight: 400;
    color:#FFA500
}

@media (max-width: 550px) {
    .form-ctn {
        margin-top: 80px;
        display: flex;
        justify-content: center;
        align-items: center ;
        flex-direction: column;
        padding: 30px 10px;
        gap: 100px;
    }
    .formdata {
        width: 80%;
    }
}

@media (max-width: 430px) {
    
.title {
    font-size: 40px;
    font-weight: 500;
    width: fit-content;
    height: fit-content;
    margin: 10px 0;
}

.desc {
    font-size: 13px;
    font-weight: 500;
    width: fit-content;
    height: fit-content;
}
.form-btn {
    margin-top: 30px;
    width:100%;
    height: fit-content;
    /* padding: 10px 36px; */
    text-align: center;
    border-radius: 8px;
    font-size: 15px;
    font-weight: 500;
    color: rgb(39, 39, 39);
    background-color: #FFA500;
    border:solid 2px  #FFA500;;
    transition: all;
    transition-duration: .3s;
    user-select: none;
    cursor: pointer;
}

.txt-ctn {
    display: flex;
    margin-top: 10px;
    width: fit-content;
    margin: 40px  auto;
}

}